.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  position: relative;
  top: -50px;
}

.movie .movie__title,
.movie .movie__year {
  font-weight: 300;
  margin: 0;
}

.movie .movie__title {
  margin-bottom: 10px;
  font-size: 24px;
  color: #535777;
}

.movie .movie__genres {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  margin: 5px 0;
}

.movie__genres li {
  margin-right: 5px;
  font-size: 14px;
}
